<template>
    <section>
        <transition-group name="list" tag="ul">
            <!-- 계속 돌려서 데이터를 보여주게 -->
            <li v-for="(todoItem, index) in propsdata" :key="todoItem" class="shadow">
                <!-- 데이터 보여지는 곳-->
                <i class="checkBtn fa fa-circle-o" aria-hidden="true"></i>
                    {{todoItem}}
                <!-- 버튼 영역 -->    
                <div class="position">   
                    <span class="completeBtn" type="button" @click="completeTodo">
                        <i class="fa fa-check" aria-hidden="true" ></i>
                    </span>
                    &nbsp;
                    <span class="updateBtn" type="button" @click="updateTodo(index)">
                        <i class="fa fa-pencil" aria-hidden="true"></i>
                    </span>
                    &nbsp;
                    <span class="removeBtn" type="button" @click="removeTodo(todoItem, index)">                 
                        <i class="fa fa-trash-o" aria-hidden="true"></i>
                    </span>
                </div>   
            </li>
        </transition-group>      
    </section>
</template>

<script>

export default {
  props: ['propsdata'],
  methods: {
    removeTodo(todoItem, index) {
      this.$emit('removeTodo', todoItem, index);
    },
    updateTodo(index){
        alert(index);
    }
  }
}
</script>

<style scoped>
    ul{
        list-style-type: none;
        padding-left: 0px;
        margin-top: 0;
        text-align: left;
    }

    li{
        display: flex;
        min-height: 50px;
        height: 50px;
        line-height: 50px;
        margin: 0.5rem 0;
        padding: 0 0.9rem;
        background: white;
        border-radius: 5px;
    }
    
    .checkBtn {
        line-height: 45px;
        color: #62acde;
        margin-right: 5px;
        
    }
    
    .removeBtn {
        margin-left: auto;
        color: #de4343;
        
    }
    
    .updateBtn {
        margin-left: auto;
        color: #6478fb;
    }
    
    .completeBtn {
        margin-left: auto;
        color: aqua;
    }
    
    .position{
        margin-left: auto;
    }

    /* 애니메이션 구현*/
    .list-enter-active,
    .list-leave-active {
    transition: all 0.5s ease;
    }
    .list-enter-from,
    .list-leave-to {
    opacity: 0;
    transform: translateX(30px);
    }

</style>
